import React, { useEffect, useState } from 'react';
import api from '../../api'

// import component
import { Table, Select, Input, Icon } from 'antd'
const { Option } = Select;
const Activity = () => {
   const [history, setHistory] = useState([])
   const [filterText, setFilterText] = useState("")
   useEffect(() => {
      getData()
   }, [])

   const getData = async (passDay) => {
      setHistory(null)
      let data = await api.activity.getHistory(passDay)
      setHistory(data)
   }

   const columns = [
      {
         title: '',
         dataIndex: 'date',
         key: 'date',
      },
      {
         title: '',
         dataIndex: 'user.name',
         key: 'name',
      },
      {
         title: '',
         dataIndex: 'inClass',
         key: 'inClass',
      },
      {
         title: '',
         dataIndex: 'action',
         key: 'action',
      },
      {
         title: '',
         dataIndex: 'detail',
         key: 'detail',
      }
   ];
   const handelSelectPassDay = (passDay) => {
      getData(passDay)
   }
   return (
      <div>
         <Select defaultValue="7" style={{ width: 120 }} onChange={handelSelectPassDay}>
            <Option value="7">7</Option>
            <Option value="10">10</Option>
            <Option value="30">30</Option>
            <Option value="90">3</Option>
            <Option value="180"></Option>
            <Option value="360"></Option>
            <Option value="100000000"></Option>
         </Select>
         <Input placeholder="" onChange={(e) => setFilterText(e.target.value)}></Input>
         {history
            ?
            <Table columns={columns} dataSource={history.filter(hist => {
               if (hist.user && hist.user.name) {
                  return (
                     hist.user.name.includes(filterText)
                  )
               }
            }
            )} />
            :
            <div style={{ display: "flex", justifyContent: "center" }}>
               <Icon type="loading" style={{ fontSize: 64, marginTop: "1rem" }} spin />
            </div>
         }
      </div>
   );
};

export default Activity;
